import React, {Suspense, useEffect, useState} from "react";
import './styles.css'
import { createUseStyles } from "react-jss";
import { Canvas } from 'react-three-fiber'
import Jc from '../components/Jc3d'
import Grid from '../components/Grid4'
import {OrbitControls} from '@react-three/drei/core'
import {ResizeObserver as polyfill} from "@juggle/resize-observer"
import hl from '../hl.wav'

const useStyles = createUseStyles({
  container: {
  },
});

const App = () => {
  const [counter, setCounter] = useState(0);
  const classes = useStyles();
  
  useEffect(() => {
    let intervalId = setInterval(() => {
        if (counter > 3) {
            return
        }
        else {
            setCounter(counter => counter + 1)
        }
    }, 1000);
    
    return () => clearInterval(intervalId);
  });
  
    return (
        <div
            className={classes.container}
            style={{backgroundColor: 'lightgray'}}
        >
            <audio autoPlay src={hl}/>
          <div
              style={{
                  width: '100vw',
                  height: '100vh',
                  backgroundColor: 'red',
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  opacity: counter < 3 ? 1 : 0,
                  transition: '3s',
                  zIndex: 100,
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'center',
                  alignItems: 'center',
                  pointerEvents: 'none'
              }}
          >
              <p>Her Language — Jaycie Rethorical</p>
          </div>
          <Canvas resize={{polyfill}} style={{width: '100vw', height: '100vh'}}>
            <perspectiveCamera position={[10, 0, 0,]}/>
            <ambientLight intensity={0.5} />
            <pointLight intensity={1} position={[-10, 0, 0]}/>
            <Suspense fallback={() => <div><h1>loafing</h1></div>}>
              <Jc />
              <Grid />
            </Suspense>
            <OrbitControls />
          </Canvas>
        </div>
    );
 
}

export default App
